import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
//import AutoCompletePlaces from '../../../core/google-map/AutoCompletePlaces';
import Loader from '../../Loader/Loader';
import SponsorsData from './SponsorsData';
import './Sponsors.css'

class Sponsors extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,      
      sponsorsList: [],
      loading: true,
      rowIndex: -1,
      formProccessing: false,
      formField: {companyName: '', email: '', contactPerson: '', last_name: '', skype_id: '', latitude: '', longitude: '',  phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '', status: '' },
      errors: {companyName: '', email: '', contact_person: '', role: '', address: '', error: ''},
      formValid: true,
      filterItem: { filter_Sponsor_id: '', country: '', state: '', custom: '', specialization: ""},

    } 
    this.handleEditSponsor = this.handleEditSponsor.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteSponsor = this.handleDeleteSponsor.bind(this);
    this.filterItemList = this.filterItemList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
    this.setLatitudeLongitude = this.setLatitudeLongitude.bind(this);
    
  }
  // Fetch the Sponsor List
  componentDidMount() { 
    this.sponsorsList();
  }

  setLatitudeLongitude(address, latLng, city = "", state = "", country = "", postal_code = ""){
    let formField = this.state.formField;
    formField.state = state;
    formField.latitude = latLng.latitude;
    formField.longitude = latLng.longitude;
    formField.country = country;formField.city = city;formField.postalCode = postal_code;
    formField.address = address
    this.setState({ formField: formField });
  }
  /*Sponsor List API*/
  sponsorsList(filterItem = {}) {
    let filterParmas = "";
    
    if(filterItem.country !== undefined && filterItem.country !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&country="+filterItem.country: "?country="+filterItem.country;
    if(filterItem.state !== undefined && filterItem.state !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&state="+filterItem.state: "?state="+filterItem.state;
    if(filterItem.custom !== undefined && filterItem.custom !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&keyword="+filterItem.custom: "?keyword="+filterItem.custom;
    if(filterItem.status !== undefined && filterItem.status !== "" ) {
      let statusText = commonService.getStatusVal(filterItem.status)
      filterParmas += (filterParmas !=="" ) ? "&status="+statusText: "?status="+statusText;
    }
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('sponsors'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, sponsorsList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    } )
  }
  /* Submit Form Handler*/
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.validateForm()){
      return false;
    }
    this.setState( { formProccessing: true}, () => {
      const formInputField = this.state.formField;
      const formData = {
        "email": formInputField.email,
        "contactPerson": formInputField.contactPerson,
        "phoneNumber": formInputField.phoneNumber, 
        "address": formInputField.address, 
        "city": formInputField.city || "", 
        "state": formInputField.state || "", 
        "latitude": formInputField.latitude || "",
        "longitude": formInputField.longitude || "", 
        "country": formInputField.country || "", 
        "postalCode": formInputField.postalCode || "", 
        "companyName": formInputField.companyName,
        "status": commonService.getStatusVal(formInputField.status)
      };
      const rowIndex = this.state.rowIndex;
      if(rowIndex > -1) {
        const sposorInfo = this.state.sponsorsList[rowIndex];
        formData.sponsorId = sposorInfo.sponsorId;
        commonService.putAPIWithAccessToken('sponsors', formData)
        .then( res => {
          
          if ( undefined === res.data.data || !res.data.status ) { 
            let errors = {};
            if(res.data.errors !== undefined)
              errors = res.data.errors;
            else
              errors.companyName = res.data.message;
            this.setState( { formProccessing: false, errors: errors} );
            //toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.sponsorsList();
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
      else{
        commonService.postAPIWithAccessToken('sponsors', formData)
        .then( res => {
          
          if ( undefined === res.data.data || !res.data.status ) { 
            let errors = {};
            if(res.data.errors !== undefined)
              errors = res.data.errors;
            else
              errors.companyName = res.data.message;
            this.setState( { formProccessing: false, errors: errors} );
            //toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.sponsorsList();
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
    } );
    
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };
  
  
  /* Validate Form */
  validateForm() {
    let formValid = false;
    let formField = this.state.formField; 
    let errors = {};
    var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
    if(formField.contactPerson === undefined || formField.contactPerson.trim() === ""){
      formValid = true;
      errors['contactPerson'] = "Contact person field required!";
    }
    if(formField.companyName === undefined || formField.companyName.trim() === ""){
      formValid = true;
      errors['companyName'] = "Compnay name field required!";
    }
    if(formField.status === undefined || formField.status.trim() === ""){
      formValid = true;
      errors['status'] = "Status field required!";
    }
    if(formField.email !== undefined && formField.email.trim() !== ""){
       if(!(pattern.test(formField.email))){
        formValid = true;
        errors['email'] = "Invalid email format";
      }
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      rowIndex: -1,
      formValid: false,
      formProccessing : false,
      formField: {companyName: '', email: '', contactPerson: '', last_name: '', phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '' },
      formErrors: {companyName: '', email: '', contact_person: '', role: '', address: '', error: ''}
    });
  }
  /* Edit Sponsor*/
  handleEditSponsor(rowIndex){
      const sposorInfo = this.state.sponsorsList[rowIndex];
      let status = (sposorInfo.status) ? "Active": "Inactive";
      const formField = {
        companyName: sposorInfo.companyName, 
        email: sposorInfo.email, 
        contactPerson: sposorInfo.contactPerson, 
        last_name: sposorInfo.lastName, 
        phoneNumber: sposorInfo.phoneNumber, 
        address: sposorInfo.address, 
        city: sposorInfo.city, 
        state: sposorInfo.state, 
        country: sposorInfo.country, 
        postalCode: sposorInfo.postalCode, 
        role: sposorInfo.roleName,
        skype_id: sposorInfo.skype_id || "",
        status: status };
      this.setState({rowIndex: rowIndex, formField: formField, modal: true, formValid: true, formProccessing: false});
  }
  /* Delete Sponsor*/
  handleDeleteSponsor(rowIndex){
    const sposorInfo = this.state.sponsorsList[rowIndex];
    this.setState( { loading : true}, () => {   
      commonService.deleteAPIWithAccessToken('sponsors', {sponsorId: sposorInfo.sponsorId})
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
         
          this.setState( { loading : false} );
          toast.error(res.data.message);
          return;
        } 
        
        this.setState({ modal: false, loading : false});
        toast.success(res.data.message);
        this.sponsorsList();
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else
          this.setState( { loading : false } );
          toast.error(err.message);
      } )
    } );
   
    
  }
  filterItemList(){
    const filterItem = this.state.filterItem;
    this.sponsorsList(filterItem);
  }
  
  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  resetSearchFilter() {
    this.setState({filterItem: { filter_Sponsor_id: '', country: '', status: '', custom: '', specialization: ''}});
    this.sponsorsList();
  }
  render() {

    const { sponsorsList, loading, modal, formProccessing, errors } = this.state;     
    let loaderElement = '';
    if(loading)        
      loaderElement = <Loader />
    const processingBtnText = <>Submit <i className="fa fa-spinner"></i></>;
    return (
      <div className="animated fadeIn">
        <Row>
          
          {loaderElement}
          <Col lg={12}>
            <Card className="oep-card">
              <CardHeader className="mainHeading">
                <strong>Sponsors List</strong> 
                <Button color="" className="categoryAdd" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add Sponsors</Button>
              </CardHeader>
                
                <Row>
                  <Col md={12}>
                    <div className="search-filter">
                    <Row>                      
                      
                      <Col md={"6"}>
                        <FormGroup> 
                          <Label htmlFor="filter_custom">Search By Company Name / Contact Person</Label>            
                          <Input type="text" placeholder="Search By Company Name / Contact Person" id="custom" name="custom" value={this.state.formField.custom} onChange={this.changeFilterHandler} />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                          <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                              <option value = "">Select</option>
                              <option value="Active">Active</option>
                              <option value="Inactive">Inactive</option>
                              <option value="Pending">Pending</option>
                          </Input>
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup className="filter-button-section"> 
                          <Button className="search-btn" type="button" onClick={this.filterItemList}>Search</Button> 
                          <Button className="search-btn" id="resetButton" type="button" onClick={this.resetSearchFilter}>Reset</Button> 
                        </FormGroup>             
                      </Col>
                    </Row>
                     </div>  
                  </Col>

                  <Col lg={12}>
                    <SponsorsData data={sponsorsList} editSponsorAction={this.handleEditSponsor} deleteSponsorAction={this.handleDeleteSponsor} dataTableLoadingStatus = {this.state.loading} />
                  </Col>  
                </Row>
              
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={this.toggle} className="full-width-modal-section organization-modal">
          <ModalHeader toggle={this.toggle}>Sponsors</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              <Row>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="companyName">Company Name</Label>            
                    <Input type="text" placeholder="Company Name *" invalid={errors['companyName'] !== undefined && errors['companyName'] !== ""} id="companyName" name="companyName" value={this.state.formField.companyName} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['companyName']}
                    </div>
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="contactPerson">Contact Person</Label>            
                    <Input type="text" placeholder="Contact Person *" invalid={errors['contactPerson'] !== undefined && errors['contactPerson'] !== ""} id="contactPerson" name="contactPerson" value={this.state.formField.contactPerson} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['contactPerson']}
                    </div>
                  </FormGroup>
                </Col>
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="email">Email</Label>            
                    <Input type="text" placeholder="Email " invalid={errors['email'] !== undefined && errors['email'] !== ""} id="email" name="email" value={this.state.formField.email} onChange={this.changeHandler} />
                    <div className="invalid-feedback">
                      {errors['email']}
                    </div>
                  </FormGroup>
                </Col>
                
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="phoneNumber">Contact Number</Label>            
                    <Input type="text" placeholder="Contact Number " id="phoneNumber" name="phoneNumber" value={this.state.formField.phoneNumber} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                 
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="address">Address</Label> 
                    <Input type="text" placeholder="Address" id="address" name="address" value={this.state.formField.address} onChange={this.changeHandler}  />
                    {/*         
                    <AutoCompletePlaces setLatitudeLongitude={this.setLatitudeLongitude} address = {this.state.formField.address} />  */}
                  </FormGroup>
                </Col>                
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="postalCode">Postal Code</Label>            
                    <Input type="text" placeholder="Postal Code" id="postalCode" name="postalCode" value={this.state.formField.postalCode} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col lg={6}>
                    <FormGroup>
                      <Label htmlFor="template_status">Status</Label>            
                      <Input type="select" placeholder="Status *" id="status" invalid={errors['status'] !== undefined && errors['status'] !== ""} name="status" value={this.state.formField.status} onChange={this.changeHandler} required >
                        <option value="">Select Status</option>
                        <option value="Active">Active</option>
                        <option value="Inactive">Inactive</option>
                        <option value="Pending">Pending</option>
                      </Input>
                      <div className="invalid-feedback">
                        {errors['status']}
                      </div>
                    </FormGroup>
                </Col>
              </Row>           
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Submit' }</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

export default Sponsors;
