import React, { Component } from 'react';

//import  { Link } from 'react-router-dom';
import MUIDataTable from "mui-datatables";
import commonService from '../../../core/services/commonService';
class SpononrsData extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: [],
    };
    
  }
  componentDidMount() {   
  }
  /* Edit Spononrs Info */
  editSpononrsItem(rowIndex){    
    this.props.editSponsorAction(rowIndex);
  }

  deleteSpononrsItem(rowIndex){    
    this.props.deleteSponsorAction(rowIndex);
  }

  render() {
    
    let rowsItem = [];    
    for(const [i, orgnization] of this.props.data.entries()){
      console.log(i);
      let orgInfo = {
        companyName: `${orgnization.companyName}`,  
        contactPerson: `${orgnization.contactPerson}`,
        email: orgnization.email || "",        
        phoneNumber: orgnization.phoneNumber || " ",
        address: orgnization.address || " ",        
        status: commonService.getStatus(orgnization.status),    
        action: "",

      }      
      rowsItem.push(orgInfo);
    }      
    const columns = [      
      {
        label: 'Company Name',
        name: 'companyName',
      },
      {
        label: 'Contact Person',
        name: 'contactPerson',
      },
      {
        label: 'Email',
        name: 'email',
      },

      {
        label: 'Phone Number',
        name: 'phoneNumber',
      },
      

       {
        label: 'Address',
        name: 'address',
      },
      {
        label: 'Status',
        name: 'status',
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            let i = tableMeta.rowIndex;
            //let rowInfo = this.props.data[i];
            //let authId = rowInfo.organizationId;
            return (
             <p><button title="Edit Spononrs" className="btn-edit" disabled={this.state.buttonProcessing} onClick={() => 
          this.editSpononrsItem(i)}><i className="fa fa-pencil"></i> </button>
          <button title="Delete Spononrs" className="btn-delete" color="warning" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to delete this Spononrs?'))
          this.deleteSpononrsItem(i)}}><i className="fa fa-trash"></i></button></p>
            );
          },
        }
      },
    ];
    const options = {
      search: false,viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: false,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      fixedHeaderOptions: { xAxis: false, yAxis: false }

    };
    
    
    return (
      <MUIDataTable
        
        data={rowsItem}
        columns={columns}
        options={options}
      />
    );
  }
}

export default SpononrsData;